export function parseBackendDate(raw: string): Date {
    if (!raw) return new Date(NaN)

    return new Date(
    raw
      .replace(" ", "T")
      .replace(/\.\d+/, m => m.slice(0, 4)) // –º–∏–ª–ª–∏—Å–µ–∫—É–Ω–¥—ã ‚Üí 3 –∑–Ω–∞–∫–∞
      .replace(/\+00:?00$/, "Z"))            // +00 or +00:00 ‚Üí Z

}

export function parseDate(date: Date): string {
    return date.toLocaleString("ru-RU", {
        day: "2-digit",
        month: "2-digit",
        year: "numeric",
        hour: "2-digit",
        minute: "2-digit",
    })
}